#contactPage {
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: auto;
    text-align: center;
    display: flex;
    flex-direction:column ;
    align-items: center;
    justify-content: center;
}

.contactPageTitle {
    font-size: 3rem;
    margin-bottom: 1rem;

}
.clientDesc {
    font-weight:300;
    font-size: 1rem;
}
.clientImgs {
    margin: 1rem 0;
    display: flex;
flex-wrap: wrap;
}

.clientImg {
    object-fit: cover;
    width: 23%;
    min-width: 9rem;
    margin: auto;
    padding: .25rem;
}
#clients {
    padding: 2rem;
}

.contactdesc {
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}

.contactForm {
margin: 1rem;
display: flex;
flex-direction: column;
justify-content: center;
width: 90vw;
max-width: 60rem;

}
.name ,.email,.msg {
    font-size: medium;
    width: 100%;
    max-width:40rem ;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: rgb(247, 89, 89);
    border: none;
    border-radius: 0.5rem;
    background-color: rgb(40,40,40);

}
.submitBtn {
border: none;
background: white;
border: none;
border-radius:0.5rem ;
margin: 2rem;
padding:.5rem;
cursor: pointer;
}

.links {
    display: flex;
    flex-wrap: wrap;

}
.link {
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.5rem;

}